.typewriter h1 {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .15em solid #33D9CB; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em; /* Adjust as needed */
  font-family: 'Source Code Pro', monospace;
  color: rgb(242, 242, 242);
  font-size:60px; 
  font-weight: bold;
  -webkit-animation: 
    typing 1.7s steps(20, end),
    blink-caret .75s step-end infinite;
          animation: 
    typing 1.7s steps(20, end),
    blink-caret .75s step-end infinite;
}

/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: #33D9CB; }
}
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: #33D9CB; }
}

/* RESPONSIVE */
@media screen and (max-width: 800px) {
  .typewriter h1 {
    font-size: 30px;
  }
}

@media screen and (min-width: 801px) and (max-width: 1024px) {
  .typewriter h1 {
    font-size: 45px;
  }
}
.subtitle {
  font-family: "Source Code Pro", monospace;
  color: #33D9CB;
  transition: opacity 2s ease-in-out 1.5s;
  opacity: 0;
  -webkit-animation: subtitle 2s 1.5s forwards;
          animation: subtitle 2s 1.5s forwards;
}

.description {
  transition: opacity 3s ease-in-out 2s;
  opacity: 0;
  -webkit-animation: subtitle 3s 2s forwards;
          animation: subtitle 3s 2s forwards;
}

@-webkit-keyframes subtitle {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes subtitle {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

